@keyframes infiniteRotation {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes pulse {
  0% { 
    transform: scale(1); 
    opacity: 1; 
  }
  50% { 
    transform: scale(calc(1 + var(--intensity, 0.2))); 
    opacity: 0.8; 
  }
  100% { 
    transform: scale(1); 
    opacity: 1; 
  }
}

@keyframes bounce {
  0%, 20%, 53%, 80%, 100% { 
    transform: translateY(0); 
  }
  40%, 43% { 
    transform: translateY(calc(-1px * var(--height, 30))); 
  }
  70% { 
    transform: translateY(calc(-0.5px * var(--height, 30))); 
  }
  90% { 
    transform: translateY(calc(-0.13px * var(--height, 30))); 
  }
}

@keyframes shake {
  0%, 100% { 
    transform: translateX(0); 
  }
  10%, 30%, 50%, 70%, 90% { 
    transform: translateX(calc(-1px * var(--intensity, 10))); 
  }
  20%, 40%, 60%, 80% { 
    transform: translateX(calc(1px * var(--intensity, 10))); 
  }
}

@keyframes float {
  0%, 100% { 
    transform: translateY(0px); 
  }
  50% { 
    transform: translateY(calc(-1px * var(--height, 20))); 
  }
}