body {
  margin: 0px;
  padding: 0px;
  background-color: #e4e4e4!important;
}
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
}
.border-box {
  border: 3px solid black;
  box-sizing: border-box;
}
#root {
  height: 100vh;
  width: 100vw;
}
html, body {
  margin: 0 !important;
  padding: 0 !important;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
  overscroll-behavior: none;
}

body {
  /* background-color: #FCEFDA; */
  position: fixed;
  inset: 0;
}

#root {
  height: 100vh;
  width: 100vw;
  position: relative;
  top: 0;
  left: 0;
}
