.container {
  height: 100vh;
  background-color: #f3f4f6;
  display: flex;
  overflow: hidden;
}
.sidebar {
  width: 300px;
  background-color: white;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  z-index: 10;
}
.right_sidebar {
  width: 100px;
  background-color: white;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  z-index: 10;
  padding: 0px;
  margin: 0px;
}
/* Main area */
.mainArea {
  flex: 1;
  display: flex;
  flex-direction: column;
  z-index: 1;
}
.canvasContainer {
  flex: 1;
  background-color: #ebedf1;
  overflow: hidden;
}
.canvasContent {
  background-color: white;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}
.canvasWrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  user-select: none;
  overflow: hidden;
}

/* Zoom instructions */
.zoomInstructions {
  position: absolute;
  bottom: 16px;
  left: 16px;
  background-color: rgba(0, 0, 0, 0.75);
  color: white;
  padding: 8px 12px;
  border-radius: 4px;
  font-size: 14px;
  pointer-events: none;
}
