/* Simple CSS for left_options_bar */

.sidebar {
  height: 100%;
  width: 100%;
  background-color: white;
  border-right: 1px solid #ccc;
  font-family: Arial, sans-serif;
}

.header {
  /* padding: 16px; */
  border-bottom: 1px solid #ccc;
  background-color: #f5f5f5;
  height:5%;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: left;
  padding-left: 10px;
}

.headerTitle {
  font-size: 18px;
  font-weight: bold;
  color: #333;
  margin: 0;
}

.searchSection {
  /* padding: 16px; */
  height: 5%;
  border-bottom: 1px solid #ccc;
  display: flex;
  align-items: center;
  justify-content: left;
  padding-left: 10px;
  padding-right: 10px;
}

.searchInput {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  box-sizing: border-box;
}

.searchInput:focus {
  outline: none;
  border-color: #007bff;
}

.tabBar {
  display: flex;
  border-bottom: 1px solid #ccc;
  background-color: #f5f5f5;
  height: 5%;
  width:100%
}
.tabButton {
  flex: 1;
  padding: 12px;
  font-size: 14px;
  font-weight: 500;
  border: none;
  cursor: pointer;
  background: transparent;
  color: #6b7280;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.tabButton:hover {
  background-color: #f3f4f6;
  color: #374151;
}

.tabButtonActive {
  background-color: #eff6ff;
  color: #1d4ed8;
  border-bottom: 2px solid #1d4ed8;
}

.content {
  padding: 16px;
  overflow-y: auto;
  height: 85%;
  display: flex;
  flex-direction: column;
}

.elementsGrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
  flex: 1;
  overflow-y: auto;
  width: 100%;
}



.elementCard {
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
  background-color: white;
  min-width: 0;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.elementCard:hover {
  border-color: #007bff;
}

.elementImage {
  width: 100%;
  height: 120px;
  display: block;
  object-fit: contain;
  background-color: #f9f9f9;
}

.elementInfo {
  padding: 8px;
  flex: 1;
}

.elementName {
  font-size: 12px;
  font-weight: bold;
  color: #333;
  margin: 0 0 2px 0;
}

.elementType {
  font-size: 10px;
  color: #666;
  margin: 0;
}

.componentList {
  display: flex;
  flex-direction: column;
  gap: 8px;
  flex: 1;
  overflow-y: auto;
}

.componentItem {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
}

.componentItem:hover {
  background-color: #f9f9f9;
  border-color: #007bff;
}

.componentIcon {
  width: 40px;
  height: 40px;
  border-radius: 4px;
  background-color: #007bff;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.componentDetails {
  flex: 1;
}

.componentName {
  font-size: 14px;
  font-weight: bold;
  color: #333;
  margin: 0 0 2px 0;
}

.componentDescription {
  font-size: 12px;
  color: #666;
  margin: 0;
}

.templateList {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.templateCard {
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
}

.templateCard:hover {
  border-color: #007bff;
}

.templatePreview {
  width: 100%;
  height: 100px;
  background-color: #007bff;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
}

.templateInfo {
  padding: 12px;
}

.templateName {
  font-size: 14px;
  font-weight: bold;
  color: #333;
  margin: 0 0 4px 0;
}

.templateDescription {
  font-size: 12px;
  color: #666;
  margin: 0 0 6px 0;
}

.templateTags {
  display: flex;
  gap: 4px;
}

.templateTag {
  font-size: 10px;
  padding: 2px 6px;
  background-color: #e6f3ff;
  color: #007bff;
  border-radius: 3px;
}

.sectionTitle {
  font-size: 14px;
  font-weight: bold;
  color: #333;
  margin: 0 0 12px 0;
  padding-bottom: 8px;
  border-bottom: 1px solid #ddd;
}

.emptyState {
  text-align: center;
  color: #666;
  padding: 32px 16px;
}

.emptyStateIcon {
  width: 48px;
  height: 48px;
  margin: 0 auto 12px auto;
  color: #ccc;
  display: block;
}

.emptyStateText {
  font-size: 14px;
  margin: 0;
}