/* element_property.module.css */

/* Layout */
.sidebar {
  height: 100%;
  width: 100%;
  background-color: white;
  border-left: 1px solid #e5e7eb;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
}

.header {
  padding: 16px;
  border-bottom: 1px solid #e5e7eb;
  background-color: #f9fafb;
}

.headerTitle {
  font-size: 18px;
  font-weight: 600;
  color: #111827;
  margin: 0;
}

/* Elements list */
.elementList {
  padding: 16px;
  border-bottom: 1px solid #e5e7eb;
  background-color: #f9fafb;
}

.elementTitle {
  font-size: 14px;
  font-weight: 500;
  color: #374151;
  margin-bottom: 8px;
  margin-top: 0;
}

.elementButton {
  width: 100%;
  padding: 8px;
  text-align: left;
  font-size: 14px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: all 0.2s;
  cursor: pointer;
  border: 1px solid #e5e7eb;
  background-color: white;
  color: #111827;
}

.elementButtonActive {
  background-color: #dbeafe;
  color: #1e40af;
  border: 1px solid #93c5fd;
}

/* Tabs */
.tabBar {
  display: flex;
  border-bottom: 1px solid #e5e7eb;
}

.tabButton {
  flex: 1;
  padding: 12px;
  font-size: 14px;
  font-weight: 500;
  border: none;
  cursor: pointer;
  background: transparent;
  color: #6b7280;
  transition: all 0.2s;
}

.tabButtonActive {
  background-color: #eff6ff;
  color: #1d4ed8;
  border-bottom: 2px solid #1d4ed8;
}

/* Content */
.content {
  flex: 1;
  overflow-y: auto;
  padding: 16px;
}

.sectionTitle {
  font-weight: 500;
  color: #111827;
  margin: 0 0 12px 0;
}
