/* Enhanced hosted_page.module.css with scroll optimizations */

.root_view_port {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 100vw;
  height: 100vh;
  overflow: auto;
  position: relative;
  scrollbar-width: none;     /* Firefox */
  -ms-overflow-style: none;  /* IE 10+ */
  
  /* Critical performance optimizations for smooth scrolling */
  transform: translateZ(0);
  will-change: scroll-position;
  contain: layout style paint;
  
  /* Enhanced mobile scrolling */
  -webkit-overflow-scrolling: touch;
  overscroll-behavior: contain;
  
  /* Reduce paint and layout thrashing */
  backface-visibility: hidden;
  perspective: 1000px;
  
  /* iOS Safari specific fixes */
  -webkit-transform: translateZ(0);
  -webkit-backface-visibility: hidden;
  -webkit-perspective: 1000px;
  
  /* Prevent iOS bounce scroll */
  overscroll-behavior-y: none;
}

.root_view_port::-webkit-scrollbar {
  display: none;
}

.scrollable_view_port {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  position: relative;
  contain: layout style;
}

.canvas {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  position: relative;
  transform-style: preserve-3d;
  
  /* Critical GPU acceleration settings */
  will-change: transform;
  transform: translateZ(0);
  backface-visibility: hidden;
  contain: layout style paint;
  
  /* Optimize rendering layers */
  isolation: isolate;
}

/* Performance optimizations for all elements */
* {
  box-sizing: border-box;
}

/* Additional scroll performance CSS */
.position-element {
  position: absolute;
 
  
  /* GPU layer promotion for smooth animations */
  will-change: transform, opacity;
  transform: translateZ(0);
  backface-visibility: hidden;
  
  /* Use CSS custom properties for dynamic updates */
  transform: translate3d(var(--pos-x, 0), var(--pos-y, 0), 0) 
             rotate(var(--rotation, 0deg));
  opacity: var(--opacity, 1);
  width: var(--width, 100px);
  height: var(--height, 100px);
  z-index: var(--layer, 0);
  
  /* Optimize composite layers */
  contain: layout style paint;
  
  /* Enhanced iOS Safari acceleration */
  -webkit-transform: translateZ(0);
  -webkit-backface-visibility: hidden;
  -webkit-perspective: 1000px;
  -webkit-overflow-scrolling: touch;
  
  /* Fix iOS Safari rendering issues */
  transform: translate3d(var(--pos-x, 0), var(--pos-y, 0), 0) rotate(var(--rotation, 0deg));
 
}

/* Smooth scroll behavior for webkit browsers */
@supports (-webkit-overflow-scrolling: touch) {
  .root_view_port {
    -webkit-overflow-scrolling: touch;
    -webkit-transform: translateZ(0);
    -webkit-backface-visibility: hidden;
  }
}

/* Hide elements that are far out of viewport for performance */
.position-element.out-of-view {
  transform: translateZ(-1000px);
  visibility: hidden;
  will-change: auto;
}

/* CSS animations for preset types */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from { 
    transform: translateY(50px);
    opacity: 0;
  }
  to { 
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes bounce {
  0%, 20%, 53%, 80%, 100% {
    transform: translate3d(0, 0, 0);
  }
  40%, 43% {
    transform: translate3d(0, -30px, 0);
  }
  70% {
    transform: translate3d(0, -15px, 0);
  }
  90% {
    transform: translate3d(0, -4px, 0);
  }
}

/* Scroll-driven animations (experimental) */
@supports (animation-timeline: scroll()) {
  .scroll-animated {
    animation-timeline: scroll(root);
    animation-range: entry 0% exit 100%;
    animation-fill-mode: both;
  }
}

/* Force hardware acceleration on mobile */
@media (max-width: 768px) {
  .root_view_port,
  .canvas,
  .position-element {
    -webkit-transform: translateZ(0);
    -webkit-backface-visibility: hidden;
    -webkit-perspective: 1000;
  }
}

/* Reset any body/html margins */
:global(body), 
:global(html) {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  
  /* Performance optimizations */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeSpeed;
}

/* Optimize image rendering */
:global(img) {
  image-rendering: -webkit-optimize-contrast;
  image-rendering: optimize-contrast;
  backface-visibility: hidden;
  transform: translateZ(0);
}

/* Swipe up animation keyframes */
@keyframes :global(swipeUpBounce) {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-200px);
  }
}

@keyframes :global(swipeUpPulse) {
  0%, 100% {
    opacity: 0.8;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.05);
  }
}

/* Additional hover effect for the swipe animation */
@keyframes :global(swipeUpGlow) {
  0%, 100% {
    filter: drop-shadow(0 0 5px rgba(255, 255, 255, 0.3));
  }
  50% {
    filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.8));
  }
}

/* Swipe animation container and element styles */
.swipeAnimationContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  animation: swipeUpPulse 2s ease-in-out infinite;
}

.swipeAnimationIcon {
  width: 80px;
  height: 80px;
  filter: invert(1);
  animation: swipeUpBounce 1.5s ease-in-out infinite;
}

.swipeAnimationText {
  color: white;
  font-size: 18px;
  margin-top: 16px;
  font-family: Arial, sans-serif;
  text-align: center;
}
